body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page-container {
text-align: center;
padding: 20px;
font-size: large;
}
.product-list {
list-style: none;
margin: 0;
padding: 0;
}
.product-list-item {
padding: 5px;
}
.header {
text-align: center;
background-color: #222;
height: 160px;
padding: 20px;
color: white;
}
.header-logo {
animation: header-logo-spin infinite 20s linear;
height: 80px;
}
@keyframes header-logo-spin {
from {
transform: rotate(0deg);
}
to {
transform: rotate(360deg);
}
}
.header-title {
font-size: 1.5em;
}
.header-link {
color: #fff;
text-decoration: none;
padding: 5px;
}
.header-link-active {
border-bottom: #ebebeb solid 2px;
}
.product-list-item a {
text-decoration: none;
}
.search-container {
text-align: right;
margin-bottom: -25px;
}
.admin-sections {
list-style: none;
margin: 0px 0px 20px 0px;
padding: 0;
}
.admin-sections li {
display: inline-block;
margin-right: 10px;
}
.admin-sections li a {
color: #222;
text-decoration: none;
}
.admin-link-active {
border-bottom: #6f6e6e solid 2px;
}

.animate-enter {
  opacity: 0;
  z-index: 1;
  }
  .animate-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
  }
  .animate-exit {
  display: none;
  }

  .product-reviews {
    list-style: none;
    padding: 0px;
    }
    .product-reviews .product-reviews-item {
    display: block;
    padding: 8px 0px;
    }
    .tabs {
      list-style: none;
      padding: 0;
      }
      .tabs li {
      display: inline-block;
      padding: 5px;
      margin: 0px 5px 0px 5px;
      cursor: pointer;
      }
      .tabs li:focus {
      outline: none;
      }
      .tabs li.active {
      border-bottom: #222 solid 2px;
      }
      .loader-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: Black;
        opacity: 0.3;
z-index: 10004;
}
.loader-circle-wrap {
position: fixed;
top: 0;
right: 0;
bottom: 0;
left: 0;
height: 100px;
width: 100px;
margin: auto;
}
.loader-circle {
border: 4px solid #ffffff;
border-top: 4px solid #899091;
border-radius: 50%;
width: 100px;
height: 100px;
animation: loader-circle-spin 0.7s linear infinite;
}
.form {
  width: 300px;
  margin: 0px auto 0px auto;
  }
  .form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  }
  .form-group label {
  align-self: flex-start;
  font-size: 16px;
  margin-bottom: 3px;
  }
  .form-group input, select, textarea {
  font-family: Arial;
  font-size: 16px;
  padding: 5px;
  border: lightgray solid 1px;
  border-radius: 5px;
  }
  .form-error {
    font-size: 13px;
    color: red;
    margin: 3px auto 0px 0px;
  }
  .form-group button {
    font-size: 16px;
    padding: 8px 5px;
    width: 80px;
    border: black solid 1px;
    border-radius: 5px;
    background-color: black;
    color: white;
    }
    .form-group button:disabled {
    border: gray solid 1px;
    background-color: gray;
    cursor: not-allowed;
    }
    .basket-summary {
      display: inline-block;
      margin-left: 10px;
      padding: 5px 10px;
      border: white solid 2px;
      }
      .like-container {
        margin-top: 20px;
        }
        .like-container button {
        margin-top: 5px;
        }